:root {
--main-radius: 5px;
--main-padding: 5px;
}
 .youtube__PFontSize{
 font-size: large;
}
.youtube__container {
display: grid;
height: 5vh;
grid-template-columns: 0.5fr 1.5fr 1fr 0.5fr;
grid-template-rows: 0.2fr 1fr 1.2fr 1fr 0.1fr;
grid-template-areas:
 "header header header header"
 "sidebar main main sidebar2"
 "sidebar content1 content1 sidebar2"
 "sidebar appUse appUse sidebar2"
 "sidebar footer footer sidebar2";
grid-gap: 0.2rem;
/* font-weight: 800; */
/* text-transform: uppercase; */
font-size: 12px;
/* color: #004d40; */
/* text-align: center; */
/* zoom: 50%; */
}
/****************/

.navBody{
   position:absolute;
   background-color:red;
   z-index:4;
 }
 .youtube__pointerEvents{
    pointer-events: none;
 }
 .p__youtube{
    font-size: large;
 }
 h2,h1{
    text-align: center; 
    }
 .youtube__box{
    width: auto;
    height: auto;
    border: 15px solid rgb(1, 17, 1);
    padding: 5px;
    padding-bottom: auto;
 }
 .youtube__hideBtnDownload{
  animation: mymove 1s infinite;
 }
 @keyframes mymove {
  from { box-shadow: 0 4px rgb(104, 109, 108);}
  to { box-shadow: 0 4px rgb(236, 5, 5);}
 }
 input{
 width: 500px;
 padding: 12px 20px;
 margin: 8px 0;
 box-sizing: border-box;
 border: 5px solid #d6e6f2
 
 }
 .youtube__button {
 display: inline-block;
 padding: 1px 25px;
 font-size: 24px;
 cursor: pointer;
 text-align: center;
 text-decoration: none;
 outline: none;
 color: white;
 background-color:#92b1d4;
 border: none;
 border-radius: 15px;
 box-shadow: 0 4px rgb(104, 109, 108);
 cursor: pointer;
 }
 
 
 .youtube__button:hover {background-color: #680b0b}
 .youtube__button:active {
 background-color: #3e8e41;
 box-shadow: 0 5px #666;
 transform: translateY(4px);
 }
 
 
 /* .................... */



.youtube__header {
   background:#b9d7ea;
   grid-area: header;
   border-radius: var(--main-radius);
   padding-top: var(--main-padding);
   text-align: center;
     line-height: 3.2rem;
}
.youtube__main{
text-align: center;

}
.youtube__appUse{
background:  #769fcd;
grid-area: appUse;
border-radius: var(--main-radius);
padding-top: var(--main-padding);
/* text-align: center; */
border: 5px solid #d6e6f2;
padding: 50px;
margin: 20px;
}
 
.youtube__mainO {
background:#769fcd;
grid-area: main;
border-radius: var(--main-radius);
padding-top: var(--main-padding);
border: 10px solid#d6e6f2;
padding: 50px;
margin: 20px;
}

#sidebar {
background: rgb(212, 212, 212);
grid-area: sidebar;
border-radius: var(--main-radius);
padding-top: var(--main-padding);
}

.youtube__about {
background:#d6e6f2;
grid-area: content1;
border-radius: var(--main-radius);
padding-top: var(--main-padding);
border: 5px solid #769fcd;
padding: 50px;
margin: 20px;
}


footer {
background: #b9d7ea;
grid-area: footer;
border-radius: var(--main-radius);
padding-top: var(--main-padding);
text-align: center;
border: 5px solid #d6e6f2;
margin: 20px;
}

