@media only screen and (max-width: 900px) {

   .p__youtube{
      font-size: small;
   }
.youtube__container {
 grid-template-columns: 1fr;
 grid-template-rows: 0.4fr 0fr 2fr 1fr      1fr  1fr;
 grid-template-areas:
   "header"
   "sidebar"
   "main"
   "content1"
   "appUse"
   "footer";
}

input{
   width: 250px;
   padding: 12px 20px;
   margin: 8px 0;
   box-sizing: border-box;
   border: 5px solid #d6e6f2
   
   }
   .youtube__mainO {
      background:#769fcd;
      grid-area: main;
      border-radius: var(--main-radius);
      padding-top: var(--main-padding);
      border: 10px solid#d6e6f2;
      padding: 5px;
      margin: 2px;
      }
}
/* end media*/

.youtube__valert {
padding: 20px;
background-color: #f44336;
color: white;
}

.youtube__closebtn {
margin-left: 15px;
color: white;
font-weight: bold;
float: right;
font-size: 22px;
line-height: 20px;
cursor: pointer; 
transition: 0.3s;
}

.youtube__closebtn:hover {
color: black;
}
